<template>
  <div>
    <el-form ref="form" :model="form" label-width="100px">
      <el-tabs v-model="activeName">
        <el-tab-pane label="基本参数" name="first">
          <div class="scroll_content">
            <el-form-item label="文档标题" style="width:500px;">
              <el-input v-model="form.title" clearable placeholder="请填写文档标题"></el-input>
            </el-form-item>
            <el-form-item label="文档类别" style="width:500px;">
              <el-select v-model="form.industry" clearable>
                <el-option label="jquery" value="8"></el-option>
                <el-option label="html" value="9"></el-option>
              </el-select>
            </el-form-item>
            <el-form-item label="积分">
              <el-input-number v-model="form.price" controls-position="right" :min="0"></el-input-number>
            </el-form-item>
            <el-form-item label="是否原创">
              <el-radio-group v-model="form.isOriginal">
                <el-radio label="0">非原创</el-radio>
                <el-radio label="1">原创</el-radio>
              </el-radio-group>
            </el-form-item>
          </div>
        </el-tab-pane>
        <el-tab-pane label="文档内容" name="second">
          <vue-ueditor-wrap
            ref="ueditor"
            v-model="form.content"
            :destroy="false"
            :config="editorConfig"
            @ready="ready"
          ></vue-ueditor-wrap>
        </el-tab-pane>
        <el-tab-pane label="预览图片" name="third">
          <div class="scroll_content">
            <el-scrollbar class="page-component__scroll">
              <el-upload
                class="upload-demo"
                ref="upload"
                action="https://jsonplaceholder.typicode.com/posts/"
                :file-list="form.fileList"
                :auto-upload="false"
                list-type="picture"
                :limit="1"
                :multiple="false"
              >
                <el-button slot="trigger" size="small" type="primary">选取文件</el-button>
                <el-button style="margin-left: 10px;" size="small" type="success">开始上传</el-button>
                <div slot="tip" class="el-upload__tip">允许上传的文件类型：jpg,jpeg,png,gif，单张图片最大上传限制：100KB</div>
              </el-upload>
            </el-scrollbar>
          </div>
        </el-tab-pane>
        <el-tab-pane label="上传文件" name="forth">
          <el-form-item label="下载类别">
            <el-checkbox-group v-model="form.download_type" @change="checkBox">
              <el-checkbox label="1">本地下载</el-checkbox>
              <el-checkbox label="2">百度云盘</el-checkbox>
              <el-checkbox label="3">360云盘</el-checkbox>
              <el-checkbox label="4">115网盘</el-checkbox>
              <el-checkbox label="5">微云网盘</el-checkbox>
              <el-checkbox label="6">码云下载</el-checkbox>
              <el-checkbox label="8">官方下载</el-checkbox>
              <el-checkbox label="99">其他下载</el-checkbox>
            </el-checkbox-group>
          </el-form-item>
          <div class="download_content">
            <el-scrollbar class="page-component__scroll">
              <!-- 本地下载 -->
              <el-form-item>
                <el-upload
                  class="upload-demo"
                  ref="upload"
                  action="https://jsonplaceholder.typicode.com/posts/"
                  :file-list="form.localFile"
                  :auto-upload="false"
                  :limit="1"
                  :multiple="false"
                  v-show="downloadFileAttr.hasLocalFile"
                >
                  <el-button slot="trigger" size="small" type="primary">本地文件</el-button>
                  <el-button style="margin-left: 10px;" size="small" type="success">开始上传</el-button>
                  <div slot="tip" class="el-upload__tip">允许上传的文件类型：zip，最大上传限制：2MB</div>
                </el-upload>
              </el-form-item>

              <!-- 百度网盘 -->
              <el-form-item label="百度网盘地址" style="width:500px;" v-show="downloadFileAttr.baiduPan">
                <el-input v-model="form.download2_url" clearable placeholder="请填写百度网盘地址"></el-input>
              </el-form-item>
              <el-form-item label="百度网盘密码" style="width:500px;" v-show="downloadFileAttr.baiduPan">
                <el-input v-model="form.download2_pwd" clearable placeholder="请填写百度网盘密码"></el-input>
              </el-form-item>

              <!-- 360网盘 -->
              <el-form-item label="360网盘地址" style="width:500px;" v-show="downloadFileAttr.Pan360">
                <el-input v-model="form.download2_url" clearable placeholder="请填写360网盘地址"></el-input>
              </el-form-item>
              <el-form-item label="360网盘密码" style="width:500px;" v-show="downloadFileAttr.Pan360">
                <el-input v-model="form.download2_pwd" clearable placeholder="请填写360网盘密码"></el-input>
              </el-form-item>

              <!-- 115网盘 -->
              <el-form-item label="115网盘地址" style="width:500px;" v-show="downloadFileAttr.Pan115">
                <el-input v-model="form.download2_url" clearable placeholder="请填写115网盘地址"></el-input>
              </el-form-item>
              <el-form-item label="115网盘密码" style="width:500px;" v-show="downloadFileAttr.Pan115">
                <el-input v-model="form.download2_pwd" clearable placeholder="请填写115网盘密码"></el-input>
              </el-form-item>
            </el-scrollbar>
          </div>
        </el-tab-pane>
      </el-tabs>
    </el-form>
  </div>
</template>

<script>
import VueUeditorWrap from "vue-ueditor-wrap"; // ES6 Module
export default {
  name: "template_form",
  components: {
    VueUeditorWrap
  },
  props: {
    artId: {
      type: Number,
      default: 0
    }
  },
  data() {
    return {
      activeName: "first",
      form: {
        title: "",
        industry: "",
        type: "",
        language: "",
        format: [],
        color: "",
        price: 0,
        isOriginal: "0",
        content: "",
        fileList: [],
        download_type: [],
        localFile: [],
        download2_url: "",
        download2_pwd: ""
      },
      editorConfig: {
        // 编辑器不自动被内容撑高
        autoHeightEnabled: false,
        // 初始容器高度
        initialFrameHeight: 440,
        // 初始容器宽度
        initialFrameWidth: "500px",
        // 上传文件接口（这个地址是我为了方便各位体验文件上传功能搭建的临时接口，请勿在生产环境使用！！！）
        // serverUrl: "http://admin.azz.lc/Public/plugin/ueditor/php/controller.php",
        // UEditor 资源文件的存放路径，如果你使用的是 vue-cli 生成的项目，通常不需要设置该选项，vue-ueditor-wrap 会自动处理常见的情况，如果需要特殊配置，参考下方的常见问题2
        // UEDITOR_HOME_URL: "/static/UEditor/",
        // 配合最新编译的资源文件，你可以实现添加自定义Request Headers,详情https://github.com/HaoChuan9421/ueditor/commits/dev-1.4.3.3
        // headers: {
        //   access_token: '37e7c9e3fda54cca94b8c88a4b5ddbf3'
        // },
        toolbars: [
          [
            "Undo",
            "Redo",
            "Bold",
            "italic",
            "underline",
            "forecolor",
            "backcolor",
            "insertorderedlist",
            "insertunorderedlist",
            "justifyleft",
            "justifycenter",
            "justifyright",
            "justifyjustify",
            "emotion",
            "simpleupload",
            "insertcode",
            "autotypeset",
            "inserttable",
            "spechars",
            "link",
            "unlink"
          ]
        ]
      },
      downloadFileAttr: {
        hasLocalFile: false,
        baiduPan: false,
        Pan360: false,
        Pan115: false
      }
    };
  },
  methods: {
    ready(editorInstance) {
      console.log(`实例${editorInstance.key}已经初始化:`, editorInstance);
      console.log(111);
    },
    checkBox(value) {
      // 本地下载
      if (value.indexOf("1") >= 0) {
        this.downloadFileAttr.hasLocalFile = true;
      } else {
        this.downloadFileAttr.hasLocalFile = false;
      }

      // 百度网盘
      if (value.indexOf("2") >= 0) {
        this.downloadFileAttr.baiduPan = true;
      } else {
        this.downloadFileAttr.baiduPan = false;
      }

      // 360网盘
      if (value.indexOf("3") >= 0) {
        this.downloadFileAttr.Pan360 = true;
      } else {
        this.downloadFileAttr.Pan360 = false;
      }

      // 115网盘
      if (value.indexOf("4") >= 0) {
        this.downloadFileAttr.Pan115 = true;
      } else {
        this.downloadFileAttr.Pan115 = false;
      }
    }
  },
  watch: {
    artId(newValue, oldValue) {
      // 这里获取数据
      console.log('新值：' + newValue);
      console.log('旧值：' + oldValue);
    }
  }
};
</script>

<style scoped>
.download_content {
  height: 400px;
  overflow: hidden;
}
</style>