<template>
  <div>
    <RightTitle title="教程文档"></RightTitle>

    <!-- 搜索表单 -->
    <div class="uar_form">
      <el-form ref="form" :inline="true" :model="filter" label-width="80px" size="medium">
        <el-form-item label="标题">
          <el-input v-model="filter.title" clearable placeholder="搜索标题"></el-input>
        </el-form-item>
        <el-form-item>
          <el-button type="primary">搜索</el-button>
        </el-form-item>
        <el-form-item>
          <el-button @click="addTemplate('')">添加文档 / 目录</el-button>
        </el-form-item>
      </el-form>
    </div>

    <!-- 数据表格 -->
    <div class="uar_form">
      <el-table :data="tableData" border style="width: 100%">
        <el-table-column label="标题" prop="title" :show-overflow-tooltip="true"></el-table-column>
        <el-table-column label="分类" prop="type" width="80" align="center"></el-table-column>
        <el-table-column label="时间" prop="time" width="120" align="center"></el-table-column>
        <el-table-column label="状态" prop="status" width="100" align="center"></el-table-column>
        <el-table-column label="操作" width="300" align="center">
          <template slot-scope="scope">
            <el-button-group class="space">
              <el-tooltip content="查看详情">
                <el-button
                  type="primary"
                  icon="el-icon-search"
                  size="small"
                  @click="showInfo(scope.row)"
                ></el-button>
              </el-tooltip>
              <el-tooltip content="编辑">
                <el-button type="primary" icon="el-icon-edit" size="small"></el-button>
              </el-tooltip>
              <el-tooltip content="删除">
                <el-button
                  type="primary"
                  icon="el-icon-delete"
                  size="small"
                  @click="deleteArt(scope.row)"
                ></el-button>
              </el-tooltip>
            </el-button-group>

            <el-button-group>
              <el-tooltip content="添加子文章">
                <el-button
                  type="primary"
                  icon="el-icon-plus"
                  size="small"
                  @click="addTemplate(scope.row)"
                ></el-button>
              </el-tooltip>
              <el-tooltip content="查看子文章">
                <el-button
                  type="primary"
                  icon="el-icon-arrow-down"
                  size="small"
                  @click="showChildArt(scope.row)"
                ></el-button>
              </el-tooltip>
              <el-tooltip content="子文章排序">
                <el-button
                  type="primary"
                  icon="el-icon-d-caret"
                  size="small"
                  @click="sortArt(scope.row)"
                ></el-button>
              </el-tooltip>
            </el-button-group>
          </template>
        </el-table-column>
      </el-table>
    </div>

    <!-- 分页 -->
    <div class="uar_form page_area">
      <el-pagination background layout="prev, pager, next" :total="1000"></el-pagination>
    </div>

    <!-- 表单内容 -->
    <el-dialog :title="dialogTitle" :visible.sync="formVisible" width="40%">
      <DocumentForm :artId="infoArtId"></DocumentForm>
      <span slot="footer" class="dialog-footer">
        <el-button @click="dialogVisible = false">取 消</el-button>
        <el-button type="primary" @click="dialogVisible = false">确 定</el-button>
      </span>
    </el-dialog>

    <!-- 查看详情 -->
    <el-dialog :title="dialogTitle" :visible.sync="infoVisible" width="40%">
      <DocumentInfo :artId="infoArtId"></DocumentInfo>
    </el-dialog>

    <!-- 查看子文章 -->
    <el-drawer :title="drawerTitle" :visible.sync="drawerShow" direction="rtl" size="50%">
      <div class="drawer_area">
        <el-table :data="gridData1" border>
          <el-table-column prop="title" label="标题"></el-table-column>
          <el-table-column prop="status" label="状态" width="100"></el-table-column>
          <el-table-column label="操作" width="300" align="center">
            <template slot-scope="scope">
              <el-button-group class="space">
                <el-tooltip content="查看详情">
                  <el-button
                    type="primary"
                    icon="el-icon-search"
                    size="small"
                    @click="showInfo(scope.row)"
                  ></el-button>
                </el-tooltip>
                <el-tooltip content="编辑">
                  <el-button type="primary" icon="el-icon-edit" size="small"></el-button>
                </el-tooltip>
                <el-tooltip content="删除">
                  <el-button
                    type="primary"
                    icon="el-icon-delete"
                    size="small"
                    @click="deleteArt(scope.row)"
                  ></el-button>
                </el-tooltip>
              </el-button-group>

              <el-button-group>
                <el-tooltip content="添加子文章">
                  <el-button
                    type="primary"
                    icon="el-icon-plus"
                    size="small"
                    @click="addTemplate(scope.row)"
                  ></el-button>
                </el-tooltip>
                <el-tooltip content="查看子文章">
                  <el-button
                    type="primary"
                    icon="el-icon-arrow-down"
                    size="small"
                    @click="showChild2Art(scope.row)"
                  ></el-button>
                </el-tooltip>
                <el-tooltip content="子文章排序">
                  <el-button
                    type="primary"
                    icon="el-icon-d-caret"
                    size="small"
                    @click="sortArt(scope.row)"
                  ></el-button>
                </el-tooltip>
              </el-button-group>
            </template>
          </el-table-column>
        </el-table>

        <!-- 分页 -->
        <div class="uar_form page_area">
          <el-pagination background layout="prev, pager, next" :total="1000"></el-pagination>
        </div>
      </div>
    </el-drawer>

    <!-- 查看二级子文章 -->
    <el-dialog :title="childArt2Title" :visible.sync="childArt2Visible" width="40%">
      <div class="drawer_area">
        <el-table :data="gridData2" border>
          <el-table-column prop="title" label="标题"></el-table-column>
          <el-table-column prop="status" label="状态" width="100"></el-table-column>
          <el-table-column label="操作" width="180" align="center">
            <template slot-scope="scope">
              <el-button-group>
                <el-tooltip content="查看详情">
                  <el-button
                    type="primary"
                    icon="el-icon-search"
                    size="small"
                    @click="showInfo(scope.row)"
                  ></el-button>
                </el-tooltip>
                <el-tooltip content="编辑">
                  <el-button type="primary" icon="el-icon-edit" size="small"></el-button>
                </el-tooltip>
                <el-tooltip content="删除">
                  <el-button
                    type="primary"
                    icon="el-icon-delete"
                    size="small"
                    @click="deleteArt(scope.row)"
                  ></el-button>
                </el-tooltip>
              </el-button-group>
            </template>
          </el-table-column>
        </el-table>

        <!-- 分页 -->
        <div class="uar_form page_area">
          <el-pagination background layout="prev, pager, next" :total="1000"></el-pagination>
        </div>
      </div>
    </el-dialog>

    <!-- 子文章排序 -->
    <el-dialog title="文章排序" :visible.sync="sortDialogVisible" width="40%">
      <div class="drawer_area">
        <el-table :data="gridData3" border>
          <el-table-column prop="title" label="标题"></el-table-column>
          <el-table-column label="排序" width="150" align="center">
            <template slot-scope="scope">
              <el-input type="text" placeholder="请输入排序值" v-model="scope.row.sort"></el-input>
            </template>
          </el-table-column>
        </el-table>
      </div>
      <span slot="footer" class="dialog-footer">
        <el-button @click="sortDialogVisible = false">取 消</el-button>
        <el-button type="primary" @click="updateSort">更新排序</el-button>
      </span>
    </el-dialog>
  </div>
</template>

<script>
import RightTitle from "../../../components/RightTitle";
import DocumentForm from "./components/DocumentForm";
import DocumentInfo from "./components/DocumentInfo";
export default {
  name: "course_document",
  components: {
    RightTitle,
    DocumentForm,
    DocumentInfo
  },
  data() {
    return {
      filter: {
        title: ""
      },
      tableData: [
        {
          id: 1,
          title: "漂亮扁平化手机APP应用官网整站模板模板",
          type: "公司企业",
          time: "2018-08-21",
          status: "审核通过"
        },
        {
          id: 2,
          title: "漂亮扁平化手机APP应用官网整站模板模板",
          type: "公司企业",
          time: "2018-08-21",
          status: "审核通过"
        }
      ],
      gridData1: [
        {
          id: 1,
          title: "漂亮扁平化手机APP应用官网整站模板模板",
          type: "公司企业",
          time: "2018-08-21",
          status: "审核通过"
        },
        {
          id: 2,
          title: "漂亮扁平化手机APP应用官网整站模板模板",
          type: "公司企业",
          time: "2018-08-21",
          status: "审核通过"
        }
      ],
      gridData2: [
        {
          id: 1,
          title: "漂亮扁平化手机APP应用官网整站模板模板",
          type: "公司企业",
          time: "2018-08-21",
          status: "审核通过"
        },
        {
          id: 2,
          title: "漂亮扁平化手机APP应用官网整站模板模板",
          type: "公司企业",
          time: "2018-08-21",
          status: "审核通过"
        }
      ],
      gridData3: [
        {
          id: 1,
          title: "漂亮扁平化手机APP应用官网整站模板模板",
          sort: 999999
        },
        {
          id: 2,
          title: "漂亮扁平化手机APP应用官网整站模板模板",
          sort: 999998
        }
      ],
      formVisible: false,
      infoVisible: false,
      dialogTitle: "添加文档",
      infoArtId: 0,
      drawerTitle: "",
      drawerShow: false,
      childArt2Title: "",
      childArt2Visible: false,
      sortDialogVisible: false
    };
  },
  methods: {
    //   添加
    addTemplate(row) {
      this.infoArtId = row.id;
      this.formVisible = true;
      this.dialogTitle = "添加文档";
    },
    showInfo(row) {
      this.infoVisible = true;
      this.dialogTitle = "文章详情";
      this.infoArtId = row.id;
    },
    deleteArt(row) {
      this.$confirm("确定要删除吗？", "提示", {
        confirmButtonText: "删除",
        cancelButtonText: "不删除",
        type: "warning"
      })
        .then(() => {
          console.log(row);
        })
        .catch(() => {});
    },
    // 查看子文章
    showChildArt(row) {
      this.drawerTitle = row.title;
      this.drawerShow = true;
    },
    // 查看二级子文章
    showChild2Art(row) {
      this.childArt2Title = row.title;
      this.childArt2Visible = true;
    },
    // 文章排序
    sortArt(row) {
      console.log(row);
      this.sortDialogVisible = true;
    },
    // 更新排序
    updateSort() {
      console.log(this.gridData3)
    }
  }
};
</script>

